.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

main {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

header {
  display: flex;
  flex-direction: row;
  height: 10vmin;
  width: 100%;
  position: fixed;
  top: 0;
  padding: 1vmin;
  justify-content: space-between;
  vertical-align: center;
  font-size: calc(10px + 1vmin);
  background-color: darkslategray;
}

footer {
  display: flex;
  flex-direction: row;
  height: 2vmin;
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 1vmin;
  align-items: center;
  justify-content: center;
  vertical-align: center;
  font-size: calc(2px + 1vmin);
  background-color: darkslategray;
}

.body {
  display: flex;
  flex-direction: row;
  margin-top: 10vmin;
  margin-bottom: 2vmin;
  padding: 1vmin;
}

.logo {
  height: 10vmin;
  pointer-events: none;
  align-self: left;
}

.banner {
  display: flex;
  flex-direction: row;
}

.user {
  display: flex;
  flex-direction: column;
  padding: 1vmin;
}

.wrapper-column {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.wrapper-row-top {
  display: flex;
  flex-direction: row;
  height: calc(100% - 24vmin);
  width: 100%;
  position: fixed;
  top: 12vmin;
}

.wrapper-row-bottom {
  display: flex;
  flex-direction: row;
  height: 8vmin;
  width: 100%;
  position: fixed;
  bottom: 4vmin;
}

.reference-wrapper {
  display: flex;
  flex-direction: column;
  width: 40%;
  overflow-y: auto;
}

.chat-wrapper {
  display: flex;
  flex-direction: column;
  width: 60%;
  overflow-y: auto;
}

.confirm-wrapper {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.submit-wrapper {
  display: flex;
  flex-direction: column;
  width: 30%;
}

@media (max-width: 768px) {
  .wrapper-row-top {
    flex-direction: column;
    position: fixed;
    height: calc(100% - 32vmin);
    width: 100%;
    top: 12vmin;
  }

  .reference-wrapper {
    flex-direction: row;
    height: 40%;
    width: 100%;
    overflow-y: auto;
  }

  .chat-wrapper {
    flex-direction: row;
    height: 60%;
    width: 100%;
    overflow-y: auto;
  }

  .wrapper-row-bottom {
    flex-direction: column; 
    position: fixed;
    height: 16vmin;
    width: 100%;
    bottom: 4vmin;
  }

  .confirm-wrapper, .submit-wrapper  {
    flex-direction: row;
    height: 8vmin;
    width: 100%;
  }
}